<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('RequestDetails') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <div class="listing mt-2">
                            <div class="table-responsive">
                                <table id="tblRequestedModuleUsers" class="table table-bordered dt-responsive nowrap mt-0">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th><span>#</span></th>
                                            <th><span>{{$t('ServiceName')}}</span></th>
                                            <th><span>{{$t('NoofUsers')}}</span></th>
                                            <th><span>{{$t('FromDate')}}</span></th>
                                            <th><span>{{$t('ToDate')}}</span></th>
                                            <th><span>{{$t('RequestMode')}}</span></th>
                                            <th><span>{{$t('InfoMode')}}</span></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                            <tr v-if="noRecord">
                                                <td colspan="7" class="text-center text-danger">
                                                    {{$t('NoRecordfound')}}
                                                </td>
                                            </tr>                                         
                                            <tr v-for="(item,index) in RequestDetails" :key="index">
                                                <td>{{item.ROWNUMBER}}</td>
                                                <td>{{item.service_name}}</td>
                                                <td>{{item.REQUEST_QUANTITY==null?0:item.REQUEST_QUANTITY}}</td>
                                                <td>{{TimeZone}}</td>
                                                <td>{{TimeZone}}</td>
                                                <td>{{item.REQUEST_MODE}}</td>
                                                <td v-if='item.REQUEST_MODE==RequestMode.Sharing'>
                                                    {{item.REQUEST_QUANTITY==null?0:item.REQUEST_QUANTITY}} 
                                                </td>
                                            </tr>
                                        
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../../services/DataService';
export default{
     props: {
        RequestId: {
            type: Number,
            required: true
        }        
    },
    data(){
        return{
            isLoading:false,
            noRecord:false,
            RequestDetails:[],
            RequestMode:{
                Transfer :1,
                Sharing : 2
            },
            TimeZone:null
        }
    },
    created:function(){   
        this.TimeZone = this.GetUserInfo.TimeZone;     
        this.FetchData();
    },
    methods:{
        FetchData:function(){            
            var ths=this;
            ths.isLoading=true;
            var url=`requestID=${ths.RequestId}&timezone`;
            DataService.GetRequestModuleForService(url).then((response)=>{                
                ths.isLoading=false;
                if(response.data.result.length>0){
                    ths.RequestDetails=response.data.result;
                    ths.noRecord=false;
                }
                else{
                    ths.RequestDetails=[];
                    ths.noRecord=true;
                }
            });
        },
        Close:function() {
            this.$parent.isRequestDetails=false;
        }
    },

}
</script>