<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="HR" SubModuleCode="EmployeeSharing" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="REQUEST_ID" :listType="['List']" :ListData="EmployeeSharingData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData" :showCheckBox="true"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'TOTAL_REQUEST_QUANTITY'"> 
                    <td class="text-center single-action">
                        <span><em class="fa fa-user-o text-dark" aria-hidden="true"></em><span class="noti-circle noti-green">  {{ data.row[data.column.COLUMN_NAME]==null?0: data.row[data.column.COLUMN_NAME]  }}</span></span>
                    </td> 
                </template>
                 <template v-if="data.column.COLUMN_NAME == 'request_type'">  
                     <span v-if="data.row[data.column.COLUMN_NAME]==1">{{$t('Resource')}}</span>
                      <span v-else-if="data.row[data.column.COLUMN_NAME]==2">{{$t('Service')}}</span>
                      <span v-else>--</span>
                </template>
            </template>
        </tg-list>  
        <RequestDetails v-if="isRequestDetails" :RequestId="requestid"></RequestDetails>     
        <RevokeDetails v-if="isRevokeDetails" :RequestId="requestid"></RevokeDetails> 
    </section>    
</template>
<script>
import DataService from '../../../services/DataService'
import Schema from './EmployeeSharingSchema'
import RequestDetails from '../../../components/Common/Modal/modal/RequestDetails.vue'
import RevokeDetails from '../../../components/Common/Modal/modal/RevokeDetails.vue'
export default {
    components:{RequestDetails,RevokeDetails},
    data(){
        return{
            isLoading:false,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,           
            searchCondition:'',
            EmployeeSharingData:[],
            Headers: [{
                "COLUMN_NAME": "REQUESTED_BY",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Requested By',
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "REQUESTED_TO_COMPANY",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Requested To Company',
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "TOTAL_REQUEST_QUANTITY",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Number of Users',
                "DISPLAY_ORDER": 3,
                settings : {
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": "REQUESTED_BY",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Requested At',
                "DISPLAY_ORDER": 3,
            },
            {
                "COLUMN_NAME": "DESCRIPTION",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Description',
                "DISPLAY_ORDER": 3,
            },
            {
                "COLUMN_NAME": "request_type",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Request Type',
                "DISPLAY_ORDER": 3,
                settings : {
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Status',
                "DISPLAY_ORDER": 3,
            }
            ],
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName:"groupname",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            isRequestDetails:false,
            requestid:null,
            isRevokeDetails:false
        }
    },
    updated() {
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white') {
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t('RequestEmployee');
            divElement[0].appendChild(spanElement);
        }
    },
    mounted() {
        var ths = this;
        ths.leftSearchFields.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },
    created:function(){                
        this.FetchData();
    },
    methods:{
        FetchData:function(){            
            var ths=this;
            ths.isLoading = true;
            var url=`pageSize=${ths.PageSize}&pageNum=${ths.PageNumber}&sortBy=${ths.SortBy}&sortExp=${ths.SortExp}&requestToCompanySearch=&projectId=`;
            DataService.GetEmployeeRequestsList(url).then((response)=>{                
                if(response.data.status=='Success'){
                    if(response.data.result.length>0){                        
                        ths.isLoading = false;
                        ths.EmployeeSharingData = response.data.result;                        
                        ths.TotalRecords = response.data.result[0].TOTAL;
                        ths.TotalPages = Math.ceil(ths.TotalRecords / ths.PageSize);
                        ths.CurrentPage = ths.PageNumber;
                        ths.listheaderbuttons[1].isdisabled = true;
                    }
                }
            });
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openAddRequest":
                    this.openAddRequest();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        commonDeleteData: function (id) {            
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("RequestEmployee")), function () {                       
                    var url=`deleteIDs=${val}`                   
                    DataService.DeleteRequest(url).then(function (response) {
                        if (response.data.result[0].Value==1) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('RequestEmployee')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('RequestEmployee')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {            
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        ResourceNotShareAndService: function(requestId, type) {            
             if (type == "NotShare") {

             }
             else{
                this.requestid = requestId;
                this.isRequestDetails=true;
             }
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case "ViewAssignedUsers":
                    this.ResourceNotShareAndService(item.REQUEST_ID, 'Service');
                    break;
                case "EditEmployeeSharing":
                    this.EditEmployeeSharing();
                    break;
                case "ViewEmployeeSharing":
                    this.ViewEmployeeSharing();
                    break;
                case "RevokeEmployeeSharing":
                    this.RevokeEmployeeSharing(item.REQUEST_ID);
                    break;
            }
        },
        RevokeEmployeeSharing:function(ths){
            this.requestid = ths;
            this.isRevokeDetails=true;
        },
        openAddRequest:function(){
             this.$router.push({ name: 'RequestEmployee' ,params: { linkedcompanyID: 0, RequestID: 0}});
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                    case 'View Assigned Users':
                        action.isVisible = true;
                    case 'View':
                        action.isVisible = true;
                    case 'Revoke':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
    }
}
</script>