<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('RevokeLicense') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <div class="listing mt-2">
                            <div class="table-responsive">
                                <table id="tblRequestedModuleUsers" class="table table-bordered dt-responsive nowrap mt-0">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th width="5%">
                                                <label class="form-check form-check-inline">
                                                    <div class="custom-control custom-checkbox custom-control-inline">
                                                        <input id="chkAll" type="checkbox" class="chkItems custom-control-input" >
                                                        <label for="chkAll" class="custom-control-label"></label>
                                                    </div>
                                                </label>
                                            </th>
                                            <th><span>{{$t('moduleName')}}</span></th>
                                            <th><span>{{$t('UserName')}}</span></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                            <tr v-if="noRecord">
                                                <td colspan="7" class="text-center text-danger">
                                                    {{$t('NoRecordfound')}}
                                                </td>
                                            </tr>                                         
                                            <tr v-for="(item,index) in RequestDetails" :key="index">
                                                <td>
                                                    <label class="form-check form-check-inline">
                                                    <div class="custom-control custom-checkbox custom-control-inline">
                                                        <input :id="'Checkbox'+item.REQUEST_ID" type="checkbox" class="chkItems custom-control-input" :value="item.REQUEST_ID">
                                                        <label :for="'Checkbox'+item.REQUEST_ID" class="custom-control-label"></label>
                                                    </div>
                                                </label>
                                                </td>
                                                <td>{{item.MODULE_NAME}}</td>
                                                <td>{{item.USER_NAME}}</td>                                                
                                            </tr>                                        
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <div class="clearfix"></div>
                        <div class="col-sm-12 col-md-6 col-lg-6 float-right text-right p-0 mt-2">
                            <div class="form-group">
                                <a href="javascript:;" @click="SaveRevoke" class="btn btn-success formbtn widthhalf"><i class="fa fa-save pr-2"></i>{{$t('Save')}}</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../../services/DataService';
export default{
     props: {
        RequestId: {
            type: Number,
            required: true
        }        
    },
    data(){
        return{
            isLoading:false,
            noRecord:false,
            RequestDetails:[],
            RequestMode:{
                Transfer :1,
                Sharing : 2
            },
            TimeZone:null
        }
    },
    created:function(){   
        this.TimeZone = this.GetUserInfo.TimeZone;     
        this.FetchData();
    },
    methods:{
        SaveRevoke:function(){            
            var val = "";
            var vm=this;
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
              if (val != null) {
                vm.confirmR(vm.$t("ConfirmRevoke"), true, (vm.$t("Delete") + "  " + vm.$t("RequestEmployee")), function () {                       
                    var url=`shareId=${vm.RequestId}`                   
                    DataService.SaveRevokeLicenses(url).then(function (response) {
                        if (response.data.result[0].Value==1) {
                            vm.ShowAlert(vm.$t('EmpRevokedSuccessfully'), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t('RequestApproval', [vm.$t('RequestEmployee')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        FetchData:function(){            
            var ths=this;
            ths.isLoading=true;
            var url=`requestID=70526`;
            DataService.GetRevokeDetail(url).then((response)=>{                
                ths.isLoading=false;
                if(response.data.result.length>0){                    
                    ths.RequestDetails=response.data.result;
                    ths.noRecord=false;
                }
                else{
                    ths.RequestDetails=[];
                    ths.noRecord=true;
                }
            });
        },
        Close:function() {
            this.$parent.isRevokeDetails=false;
        }
    },

}
</script>